
import store from '../store/index'
const BASE_URL =
  process.env.NODE_ENV === 'development'
    ? 'https://ihuphtdq.ngrok.do-it.cn'
    : 'https://ihuphtdq.ngrok.do-it.cn'

const showToast = (title) => {
  uni.showToast({
    title: title,
    icon: 'none'
  })
}
const showModal = (content) => {
  uni.showModal({
    title: '',
    content: content,
    showCancel: false,
    success: function (res) {
      return
    }
  })
}
/** 
 * @method http
 * @param {String} url 
 * @param {Object} data 
 * @param {Object} option 
 * @return {Object} Promise
 */
const http = (url, data = {}, option = {}) => {
  console.log(option)
  const hideLoading = option.hideLoading || false //  loading
  const hideMsg = option.hideMsg || false // 
  const method = option.method && (option.method.toUpperCase() || 'GET')	//	
  const header = option.header || { 'content-type': 'application/x-www-form-urlencoded' }
  // const token = uni.getStorageSync('token') //  token
  if (!hideLoading) {
    uni.showLoading({
      title: '...',
      mask: true
    })
  }
  return new Promise((resolve, reject) => {
    uni.request({
      url: BASE_URL + url,
      method: method, //  post 
      header: {
        // 'Authorization': token,
        ...header,
        'Debug-User': 'of4Qa44Z6ivVljeJSHmG2FhOuYas'
      },
      data: data,
      success: res => { // 
        if (!hideLoading) uni.hideLoading()
        if (res.statusCode === 200) {
          const result = res.data
          if (result.state === 'ok') {
            if (!store.state.isToken) {
              store.commit('toggleIsToken', true)
            }
            return resolve(result)
          }
          if (result.state.includes('TOKEN')) {
            store.commit('toggleIsToken', false)
            uni.removeStorageSync('token')
          }
          reject(result)
          if (!hideMsg) showModal(result.msg)
        } else { //  200
          showToast('[' + res.statusCode + '] ')
          reject('[' + res.statusCode + '] ')
        }
      },
      fail: (err) => { // 
        if (!hideLoading) uni.hideLoading()
        if (err.errMsg !== 'request:fail abort') {
          showToast('')
          reject('')
        }
      }
    })
  })
}
export default http

